
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
Loading
},
data() {
return {
loading: true
};
},
beforeRouteEnter(to, from, next) {
next( vm =>{
this.loading = false;
});
}
};



